// –í–∏–±–∏—Ä–∞—î–º–æ –ø–æ—Ç—Ä—ñ–±–Ω—ñ –µ–ª–µ–º–µ–Ω—Ç–∏
const burgerMenu = document.querySelector('.burger-menu');
const mobileMenu = document.querySelector('.mobile-menu');
const mobileMenuClose = document.querySelector('.mobile-menu-close');
const overlay = document.querySelector('.overlay');
const mobileLinks = document.querySelectorAll('.nav-list-m a');

// –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –≤—ñ–¥–∫—Ä–∏—Ç—Ç—è –º–µ–Ω—é
function openMenu() {
  mobileMenu.classList.add('active');
  overlay.classList.add('active');
  document.body.classList.add('lock');
}

// –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –∑–∞–∫—Ä–∏—Ç—Ç—è –º–µ–Ω—é
function closeMenu() {
  mobileMenu.classList.remove('active');
  overlay.classList.remove('active');
  document.body.classList.remove('lock');
}

// –í—ñ–¥–∫—Ä–∏–≤–∞—î–º–æ –º–µ–Ω—é –ø–æ –∫–ª—ñ–∫—É –Ω–∞ –±—É—Ä–≥–µ—Ä
burgerMenu.addEventListener('click', openMenu);

// –ó–∞–∫—Ä–∏–≤–∞—î–º–æ –º–µ–Ω—é –ø–æ –∫–ª—ñ–∫—É –Ω–∞ —Ö—Ä–µ—Å—Ç–∏–∫
mobileMenuClose.addEventListener('click', closeMenu);

// –ó–∞–∫—Ä–∏–≤–∞—î–º–æ –º–µ–Ω—é –ø–æ –∫–ª—ñ–∫—É –Ω–∞ overlay
overlay.addEventListener('click', closeMenu);

// –ó–∞–∫—Ä–∏–≤–∞—î–º–æ –º–µ–Ω—é —ñ —Å–∫—Ä–æ–ª–∏–º–æ –¥–æ —Å–µ–∫—Ü—ñ—ó –ø—Ä–∏ –∫–ª—ñ–∫—É –Ω–∞ –ø—É–Ω–∫—Ç
mobileLinks.forEach(link => {
  link.addEventListener('click', function (e) {
    e.preventDefault();
    const targetId = this.getAttribute('href');
    const targetSection = document.querySelector(targetId);

    if (targetSection) {
      targetSection.scrollIntoView({ behavior: 'smooth' });
    }

    closeMenu();
  });
});
